import React, { useState, useEffect } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import _ from 'lodash';
import gsap from 'gsap';

function Vr(props) {
  const [ currentHouseName, setCurrentHouseName ] = useState();
  const [ initialized3D, setInitialized3D ] = useState(false);
  const [ camera, setCamera ] = useState();
  const [ scene, setScene ] = useState();
  const [ currentRouteBtns, setCurrentRouteBtns ] = useState([]);
  const [ currentRouteSprites, setCurrentRouteSprites ] = useState([]);

  useEffect(() => {
    if (!initialized3D) {
      init();
      setInitialized3D(true);
    }
  });

  const init = () => {
    const { houses, axes } = props.data;

    // 
    let camera;
    let renderer;
    let scene;
    let ambientLight;
    let orbitControl;
    let model;
    let axesHelper;

    renderer = initRenderer();
    scene = initScene();
    camera = initCamera(45, { x: -0.5, y: 0, z: 0 });
    ambientLight = initAmbientLight(0xffffff, 1);
    model = initMainHouse();
    orbitControl = initOrbitControl(camera, renderer);
    if (axes) {
      axesHelper = initAxesHelper();
      scene.add(axesHelper);
    }

    // 
    const mainHouseParams = getHouseByType('main');
    if (!mainHouseParams) {
      mainHouseParams = houses[0];
    }

    // 
    const { meshs, sprites } = createRouteBtnsAndSprites(mainHouseParams.name);

    // 
    scene.add(ambientLight);
    scene.add(model);
    _.forEach(meshs, function (routeBtn) {
      scene.add(routeBtn);
    });
    _.forEach(sprites, function (routeSprite) {
      scene.add(routeSprite);
    });

    // state
    setCamera(camera);
    setScene(scene);
    setCurrentRouteBtns(meshs);
    setCurrentRouteSprites(sprites);
    setCurrentHouseName(mainHouseParams.name);

    const animate = () => {
      requestAnimationFrame(animate);
      orbitControl.update();
      renderer.render(scene, camera);
    }

    // 
    animate();
  }

  // 
  const initMainHouse = () => {
    const mainHouseParams = getHouseByType('main');

    // 
    const geometry = new THREE.SphereGeometry(1, 32, 32);

    // 
    const material = new THREE.MeshPhongMaterial();
    const texture = new THREE.TextureLoader().load(mainHouseParams.texture);
    texture.wrapS = THREE.RepeatWrapping;
    texture.wrapT = THREE.RepeatWrapping;
    material.map = texture;

    const model = new THREE.Mesh(geometry, material);

    // 
    model.geometry.scale(1, 1, -1);
    model.name = 'house';

    return model;
  }

  // 
  const changeHouseTexture = (houseName) => {
    const house = scene.getObjectByName('house');
    const houseParams = getHouseByName(houseName);
    const texture = new THREE.TextureLoader().load(houseParams.texture);
    texture.wrapS = THREE.RepeatWrapping;
    texture.wrapT = THREE.RepeatWrapping;
    house.material.map = texture;
  }

  // 
  const getHouseByName = (name) => {
    const { houses } = props.data;
    const targetHouse = _.find(houses, (o) => o.name == name);
    return targetHouse;
  }

  // 
  const getHouseByType = (type) => {
    const { houses } = props.data;
    const targetHouse = _.find(houses, (o) => o.type == type);
    return targetHouse;
  }

  // 
  const createRouteBtnsAndSprites = (houseName) => {
    const targetHouse = getHouseByName(houseName);
    let routeBtnsMesh = [];
    let sprites = [];
    _.forEach(targetHouse.routes || [], function(route) {
      // 
      let canvas = document.createElement('canvas');
      const width = 180;
      const height = 40;
      let ctx = canvas.getContext('2d');
      ctx.fillStyle = "#000000";
      ctx.fillRect(0, 0, width, height);
      ctx.fillStyle = '#ffffff';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle';
      ctx.font = '35px Arial';
      ctx.fillText(route.label, width / 2, height / 2);

      const texture = new THREE.CanvasTexture(canvas);
      const spriteMaterial = new THREE.SpriteMaterial({
        map: texture,
        transparent: true,
        opacity: 0.5
      });
      const sprite = new THREE.Sprite(spriteMaterial);
      sprite.scale.set(0.2, 0.2, 1);
      sprite.position.set(route.position.x, route.position.y, route.position.z);
      sprites.push(sprite);

      // 
      let planeGemetry = new THREE.CircleGeometry(0.02, 32);
      let planeMaterial = new THREE.MeshBasicMaterial({
        color: 0xffffff,
        side: THREE.DoubleSide
      });
      let planeMesh = new THREE.Mesh(planeGemetry, planeMaterial);
      planeMesh.position.set(route.position.x, route.position.y, route.position.z);
      planeMesh.rotateX(0.5 * Math.PI);
      planeMesh.name = route.to;
      routeBtnsMesh.push(planeMesh);
    });
    return {
      meshs: routeBtnsMesh,
      sprites: sprites
    };
  }

  // 
  const initCamera = (fov, position) => {
    const camera = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, 0.1, 10000);
    camera.position.set(position.x, position.y, position.z);
    camera.updateProjectionMatrix();
    return camera;
  }

  // 
  const initRenderer = () => {
    const renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
      canvas: document.getElementById('vr-canvas')
    });
    renderer.setSize(window.innerWidth, window.innerHeight);
    return renderer;
  }

  // 
  const initScene = () => {
    const scene = new THREE.Scene();
    scene.background = new THREE.Color(1, 1, 1);
    return scene;
  }

  // 
  const initAmbientLight = (color, intensity = 0.1) => {
    const ambientLight = new THREE.AmbientLight(color, intensity);
    return ambientLight;
  }

  // 
  const initOrbitControl = (camera, renderer) => {
    const control = new OrbitControls(camera, renderer.domElement);
    return control;
  }

  const initAxesHelper = (size = 1) => {
    const axesHelper = new THREE.AxesHelper(size);
    return axesHelper;
  }

  // 
  const handleCanvasClick = (event) => {
    const raycaster = new THREE.Raycaster();
    const pointer = new THREE.Vector2();
    //  x y (-1, 1)
    pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    pointer.y = - (event.clientY / window.innerHeight) * 2 + 1;
    // 
    raycaster.setFromCamera(pointer, camera);

    // 
    let intersects = raycaster.intersectObjects(scene.children);
    console.log(intersects);
    const currentHouse = getHouseByName(currentHouseName);
    const eventObject = _.find(intersects, function(intersect) {
      const intersectRoute = _.find(currentHouse.routes, function(route) {
        return route.to == intersect.object.name;
      })
      return !!intersectRoute;
    });
    const houseTarget = _.filter(intersects, function(o) { return o.object.name == 'house' });
    if (eventObject) {
      handleRouteBtn(eventObject.object.name);
    }
    
    console.log('', houseTarget[0].point);
  }

  const handleRouteBtn = (houseName) => {
    let fov = {
      angle: 45
    };
    gsap.to(fov, {
      angle: 20,
      duration: 0.5,
      onUpdate: () => {
        camera.fov = fov.angle;
        camera.updateProjectionMatrix();
      },
      onComplete: () => {
        clearCurrentHouseScene();
        changeHouseTexture(houseName);
        
        // 
        const { meshs, sprites } = createRouteBtnsAndSprites(houseName);
        
        //state
        setCurrentRouteBtns(meshs);
        setCurrentRouteSprites(sprites);
        setCurrentHouseName(houseName);

        // 
        _.forEach(meshs, function(routeBtn) {
          scene.add(routeBtn);
        });
        _.forEach(sprites, function(routeSprite) {
          scene.add(routeSprite);
        });

        camera.fov = 45;
        camera.updateProjectionMatrix();
      }
    });
  }

  // 
  const clearCurrentHouseScene = () => {
    _.forEach(currentRouteBtns, function(routeBtn) {
      scene.remove(routeBtn);
    });
    _.forEach(currentRouteSprites, function(routeSprite) {
      scene.remove(routeSprite);
    });
  }

  return (
    <canvas id='vr-canvas' onClick={handleCanvasClick}></canvas>
  );
}

export default Vr;